.projects{
    font-family: 'Poppins',sans-serif;
    background-color: #000;
    color: #fff;

    .serv-content{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;

        .card{
            width: calc(33% - 20px) ;
            text-align: center;
            border-radius: 6px;
            padding: 20px 25px;
              
            transition: all .3s ease;
            
            &:hover{
                background-color: #2c7ae7;

                i{
                    color: #fff;
                }
            }

            i{
                font-size: 50px;
                color: #2c7ae7;
                transition: color .3 ease;                
            }

            .text{
                font-size:25px;
                font-weight: 500;
                margin: 10px 0 7px 0;
            }

            .box{
                transition: all .3s ease;
                position: relative;

                img{
                    width: 90%;
                }

                &:hover{
                    transform:scale(1.05);;
                }

                p{
                    text-align: justify;
                    margin-bottom: 25px;
                }
            }

            .box-btn{
                    position: absolute;
                    top: 0;
                    width: 100%;
                    display: flex;
                    justify-content: space-between;

                a{
                    height: auto;
                    width: 25%;
                    display: inline-block;
                    background-color: #000;
                    color: #fff;
                    font-size: 13px;
                    padding: 5px;
                    border-radius: 6px;
                    transition: all .3s ease; 

                    &:hover{
                        background-color: #fff;
                        color: #000;
                    }
                }
            }
        }
    }


    @media(max-width:1003px){
        .serv-content .card {
            width: calc(50% - 10px);
            margin-bottom: 20px;
        }
        .container{
            padding: 0 23px;
        }
    }

    @media(max-width:700px){

        
        .serv-content .card{
            width: 100%;
        }
    }
}